:root {
  --pink-color: #ffbff7;
  --yellow-color: #fcd418;
  --blue-color: #2e3192;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  width: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.clicable {
  cursor: pointer;
}

.non-sel {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.footer {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	background-color: var(--yellow-color);
}

.footer-logos {
	margin: 25px;
	width: 500px;
	height: 40px;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
}

.footer-logo {
	height: 100%;
}

.injected-svg {
	height: 40px;
	width: auto;
	width: initial;
}
.navbar {
	width: 100%;
	height: 65px;
}

.navbar-content {
	position: fixed;
	display: flex;
	justify-content: center;
	width: 100%;
	min-height: 65px;
	background-color: var(--pink-color);
}

.navbar-delimiter {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	width: 1000px;
	height: 100%;
	font-size: 20pt;
	color: white;
}

.navlink {
	flex: 0 1 17%; /*flex: 0 1 20%;*/
    display: none;
	justify-content: center;
	align-items: center;
	min-height: 65px;
}

.navimg {
	flex: 1 1 30%;
    display: flex;
	justify-content: center;
    align-items: center;
	min-height: 65px;
}

.navimg img {
    max-height: 38px;
    max-width: 165px;
}

.navtop {
	display: flex;
	align-items: center;
	justify-content: center;
	flex: 0 1 50%;
}

.navtop-side {
	flex: 0 0 10%;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 10px;
}

.navmenu-open {
	display: flex;
}

@media screen and (min-width: 600px) {
	.navbar-delimiter {
		flex-direction: unset;
	}

	.navtop-side {
		display: none;
	}

	.navlink {
		display: flex;
	}
}
.background {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 350px;
	background-repeat: repeat-y;
	background-attachment: fixed;
	background-size: cover;
}

.background img {
	background-color: white;
	border-radius: 50%;
	max-height: 200px;
}
.mission {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;
}

.mission-child {
	flex: 1 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin: 10% 15%;
	min-height: 200px;
}

.mission-text {
	font-size: 25px;
}

.mission-button-child {
	display: flex;
	justify-content: center;
	align-items: center;
	flex: 1 1;
}

.mission-button-text {
	color: var(--blue-color);
	font-size: 30pt;
	font-weight: bold;
	text-align: center;
}

.mission-button {
	border-radius: 8px;
	font-size: 20px;
	background-color: lightseagreen;
	color: white;
	border: none;
	padding: 10px 40px;
	text-align: center;
}

@media screen and (min-width: 600px) {
	.mission {
		flex-direction: row;
	}

	.mission-child {
		min-height: 300px;
		margin: 5% 5%;
	}
}
.banner {
	align-items: center;
	justify-content: center;
	display: flex;
	overflow: hidden;
}

.big-banner img {
	width: 100%;
}

.big-banner {
	width: 100%;
}

.small-banner {
	height: 100%;
	flex: 1 0;
	display: flex;
	flex-direction: column;
}

.small-banner-img {
	display: flex;
	height: 200px;
	flex: 1 0;
	justify-content: center;
	align-items: center;
	max-width: 100%;
}

.small-banner-img img {
	max-height: 95%;
	max-width: 100%;
}

.small-banner-text {
	flex: 0 0;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
}

.small-banners {
	display: flex;
	flex-direction: row;
}
